export default {
	props:{
		title:{
			type:String,
			default:'提示'
		},
		placeholder:{
			type:String,
			default:'请点击输入'
		},
		name:{
			type:String,
			default:'text'
		},
		type:{
			type:String,
			default:'text'
		},
		value:{
			type:String,
			default:''
		},
		cancelColor:{
			type:String,
			default:'#999999'
		},
		confirmColor:{
			type:String,
			default:'#333333'
		},
		cancelText:{
			type:String,
			default:'取消'
		},
		confirmText:{
			type:String,
			default:'确定'
		},
		maxlength:{
			type:Number,
			default:5
		}
	},
	data() {
		return {

		};
	},
	methods: {
		formSubmit: function(e) {
			console.log(e)
			let _formdata = e.detail.value
			this.$emit('confirm',_formdata)
		},
		formReset: function(e) {
			this.$emit('cancel')
		}
	}
}
